import React, { useState, useEffect } from 'react';
import styles from './FirstPage.module.scss';
import { Title } from './Title/Title';
import { Counter } from './Counter/Counter';
import { ScrollArrow } from '../utils/ScrollArrow/ScrollArrow';

interface FirstPageProps {
  showText: boolean;
  showNumber: boolean;
  number: number;
  showYear: boolean;
  showArrow: boolean;
  onScrollClick: () => void;
  pageRef: React.RefObject<HTMLDivElement>;
}

export const FirstPage: React.FC<FirstPageProps> = ({
  showText,
  showNumber,
  number,
  showYear,
  showArrow,
  onScrollClick,
  pageRef,
}) => {
  const [bgLoaded, setBgLoaded] = useState(false);
  const [bgUrl, setBgUrl] = useState('');

  useEffect(() => {
    const checkFormatSupport = async () => {
      try {
        // –ü—Ä–æ–≤–µ—Ä—è–µ–º –ø–æ–¥–¥–µ—Ä–∂–∫—É AVIF
        const avifSupported = await supportsAvif();
        const format = avifSupported ? 'avif' : 'jpg';
        const imageUrl = `/lesyayear/images/back/page1.${format}`;
        setBgUrl(imageUrl);

        // –ü—Ä–µ–¥–∑–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
        const img = new Image();
        img.src = imageUrl;
        img.onload = () => setBgLoaded(true);
        img.onerror = () => {
          // –§–æ–ª–±—ç–∫ –Ω–∞ JPG –µ—Å–ª–∏ –≤—ã–±—Ä–∞–Ω–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –Ω–µ –∑–∞–≥—Ä—É–∑–∏–ª—Å—è
          const fallbackUrl = '/lesyayear/images/back/page1.jpg';
          setBgUrl(fallbackUrl);
          const fallbackImg = new Image();
          fallbackImg.src = fallbackUrl;
          fallbackImg.onload = () => setBgLoaded(true);
        };
      } catch (error) {
        console.error('Error loading background image:', error);
        setBgUrl('/lesyayear/images/back/page1.jpg');
        setBgLoaded(true);
      }
    };

    checkFormatSupport();
  }, []);

  return (
    <div
      ref={pageRef}
      className={`${styles.firstPage} ${bgLoaded ? styles.bgLoaded : ''}`}
      style={{ '--bg-image': bgUrl ? `url(${bgUrl})` : 'none' } as React.CSSProperties}
    >
      <div className={styles.content}>
        <Title show={showText} />
        <Counter show={showNumber} number={number} showYear={showYear} />
        <ScrollArrow show={showArrow} onClick={onScrollClick} />
      </div>
    </div>
  );
};

// –§—É–Ω–∫—Ü–∏—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –ø–æ–¥–¥–µ—Ä–∂–∫–∏ AVIF
async function supportsAvif() {
  if (!self.createImageBitmap) return false;

  const avifData = 'data:image/avif;base64,';
  
  try {
    const blob = await fetch(avifData).then((r) => r.blob());
    await createImageBitmap(blob);
    return true;
  } catch {
    return false;
  }
}