import React, { useEffect, useState } from 'react'
import styles from './PageTransition.module.scss'

interface PageTransitionProps {
  onTransitionEnd: () => void
  onScrollStart: () => void
  isVisible: boolean
  direction?: 'up' | 'down'
}

export const PageTransition: React.FC<PageTransitionProps> = ({ 
  onTransitionEnd, 
  onScrollStart, 
  isVisible,
  direction = 'down' 
}) => {
  const [isAnimating, setIsAnimating] = useState(true)

  useEffect(() => {
    // –°–Ω–∞—á–∞–ª–∞ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –∞–Ω–∏–º–∞—Ü–∏—é
    const showAnimation = setTimeout(() => {
      // –ü–æ—Å–ª–µ –Ω–µ–±–æ–ª—å—à–æ–π –∑–∞–¥–µ—Ä–∂–∫–∏ –Ω–∞—á–∏–Ω–∞–µ–º —Å–∫—Ä–æ–ª–ª
      onScrollStart()
    }, 400)

    // –ê–Ω–∏–º–∞—Ü–∏—è –¥–ª–∏—Ç—Å—è 1.5 —Å–µ–∫—É–Ω–¥—ã
    const hideAnimation = setTimeout(() => {
      setIsAnimating(false)
      onTransitionEnd()
    }, 800)

    return () => {
      clearTimeout(showAnimation)
      clearTimeout(hideAnimation)
    }
  }, [onTransitionEnd, onScrollStart])

  return (
    <>
      <div className={`${styles.transition} ${isAnimating ? styles.animate : ''} ${direction === 'up' ? styles.up : ''}`}>
        <div className={styles.heart}>‚ù§Ô∏è</div>
      </div>
      <div style={{ visibility: isVisible ? 'visible' : 'hidden' }}>
        {/* –ó–¥–µ—Å—å –±—É–¥—É—Ç –±–ª–æ–∫–∏ */}
      </div>
    </>
  )
} 