import React, { useEffect, useState } from 'react'
import styles from './OptimizedImage.module.scss'

interface OptimizedImageProps {
  avif: string
  webp: string
  jpg: string
  alt: string
  className?: string
}

export const OptimizedImage: React.FC<OptimizedImageProps> = ({ avif, webp, jpg, alt, className }) => {
  const [isLoaded, setIsLoaded] = useState(false)

  useEffect(() => {
    const img = new Image()
    img.onload = () => setIsLoaded(true)
    img.src = avif // –ü—Ä–æ–±—É–µ–º —Å–Ω–∞—á–∞–ª–∞ AVIF
  }, [avif])

  if (!isLoaded) {
    return <div className={`${styles.placeholder} ${className || ''}`} />
  }

  return (
    <picture className={className}>
      <source srcSet={avif} type="image/avif" />
      <source srcSet={webp} type="image/webp" />
      <img src={jpg} alt={alt} className={styles.image} loading="lazy" />
    </picture>
  )
} 