import React, { useEffect, useState } from 'react'
import styles from './OptimizedBackground.module.scss'

interface OptimizedBackgroundProps {
  image: {
    avif: string
    webp: string
    jpg: string
  }
}

export const OptimizedBackground: React.FC<OptimizedBackgroundProps> = ({ image }) => {
  const [isLoaded, setIsLoaded] = useState(false)
  const [error, setError] = useState(false)

  useEffect(() => {
    const img = new Image()
    
    const handleLoad = () => setIsLoaded(true)
    const handleError = () => setError(true)
    
    img.onload = handleLoad
    img.onerror = handleError
    
    // –ü—Ä–æ–±—É–µ–º —Ñ–æ—Ä–º–∞—Ç—ã –ø–æ –æ—á–µ—Ä–µ–¥–∏
    img.src = image.avif
    
    return () => {
      img.onload = null
      img.onerror = null
    }
  }, [image])

  if (error || !isLoaded) {
    return <div className={styles.placeholder} />
  }

  return (
    <div 
      className={styles.background}
      style={{
        '--avif-url': `url(${image.avif})`,
        '--webp-url': `url(${image.webp})`,
        '--jpg-url': `url(${image.jpg})`,
      } as React.CSSProperties}
    />
  )
}